export default {
	data() {
		return {
			flag: 0,
			childs: [{ id: '', name: '' }, { id: '', name: '' }, { id: '', name: '' }],
			childIds: ['', '', ''],
			childIdName: []
		};
	},
	onLoad(params) {
		let count = parseInt(params.childCount);
		console.log(count);
		this.childs = this.childs.slice(0, count);
		this.childIds = this.childIds.slice(0, count);
		this.$forceUpdate()
		console.log(this.childs);
	},
	methods: {
		handleBind() {
			console.log('绑定');
			console.log(this.childIds);
			let rows = this.childIds.map(r => {
				return {
					studentId: r,
					parentId: uni.getStorageSync('ID')
				}
			})
			if (!this.flag) {
				this.flag ++
				uni.request({
					url: 'http://127.0.0.1:8888/familyNew',
					method: 'POST',
					data: {
						rows
					},
					success() {
						uni.showToast({
							title: '绑定成功',
							duration: 1000
						})
						uni.showToast({
							title: '请等待审核',
							duration: 2000,
							success() {
								uni.navigateTo({
									url: '/pages/init/init'
								})
							}
						})
					},
					fail(err) {
						console.log(err);
						uni.showToast({
							title: '绑定失败'
						})
						that.flag = 0
					}
				})
			}
		},
		handleCompleteInput(index, value) {
			console.log(index, value.target.value);
			this.childIds[index] = value.target.value
		}
	}
};
